.sidebar__brand h3 {
  padding-top: 8px;
  padding-left: 22px;
  padding-bottom: 13px;
}

.sidebar__brand-list-ul {
  width: 240px;
  height: 175px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  overflow: auto; 
}

.sidebar__brand-list-ul-li {
  padding-top: 12px;
}

/*Кастомизация плейсхолдера*/
input::-webkit-input-placeholder { 
  color: #D6D6D6; 
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

input:-moz-placeholder {
  color: #D6D6D6; 
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.submit {
  position: absolute;
  top: -1px;
  right: 30px;
  width: 35px;
  height: 35px;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  background: url(../img/catalogue-pics/product-catalogue-sprite-min.png) no-repeat;
  background-position: -14px -52px;
}

/*Чекбокс "Со скидкой"*/
span.checkbox-discount {
  position: relative;
  display: inline-block;
  top: -2px;
  left: 22px;
  width: 19px;
  height: 19px;
  vertical-align: middle;
  border: 1px solid #333333;
}

.checkbox:checked + .checkbox-discount::before {
  position: absolute;
  top: -1px;
  left: -1px;
  content: url(../img/catalogue-pics/checkbox-checked-19.png);
  display: block;
  width: 16px;
  height: 16px;
}

.text-discount {
  display: inline-block;
  margin-left: 25px;
  margin-top: 40px;
  margin-bottom: 23px;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  font-weight: 400;
}

/*Сброс*/
.drop-down {
  display: block;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  margin: 0 auto;
  padding-bottom: 5px;
  font-size: 18px;
  text-align: center;
}

.drop-down-icon {
  position: relative;
  top: -1px;
  right: 1px;
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: top;
  background: url(../img/catalogue-pics/product-catalogue-sprite-min.png) no-repeat;
  background-position: -60px -19px;
}

.drop-down a {
  position: relative;
  top: 10px;
  right: 2px;
  display: inline-block;
  text-decoration: underline;
  vertical-align: top;
}

/*Кастомизация бегунков*/
::-webkit-scrollbar-button {
background-image: url('');
background-repeat: no-repeat;
width: 7px;
height: 7px;
}

::-webkit-scrollbar-track {
background-color: rgba(255, 255, 255, 0.5);
}

::-webkit-scrollbar-thumb {
border-radius: 15px;
background-color: #A1B3BF;
}

::-webkit-scrollbar-thumb:hover {
background-color: rgba(161, 179, 191, 0.5);
}

::-webkit-resizer {
background-image: url('');
background-repeat: no-repeat;
width: 7px;
}

